/* Transition */
$prefix: '.rise-blocks';

#{$prefix}-block-lists{
    > div{
        transition: all 0.3s linear;
    }
}

#{$prefix}-rectangle-layout{
    #{$prefix}-block-lists{
        #{$prefix}-profile-card{
            overflow: hidden;
        }
    }
}

#{$prefix}-circle-layout{
    > .rise-blocks-parent-block-selector {
        margin-bottom: 90px;
    }
}

#{$prefix}-border-enable{
    .block-editor-block-list__layout{
        > div:not(.block-list-appender){
            transition: all 0.3s linear;
            border: 1px solid #ddd;
        }
    }
}

#{$prefix}-items-per-row-1{
    .block-editor-block-list__layout{
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
    }
    .rise-blocks-profile-card{
        max-width: 100%;
        flex: 0 0 100%;
    }
}

